@import "../../styling/variables.scss";

.Toggle {
  display: grid;
  grid-template-columns: calc(50% - 20px) 40px calc(50% - 20px);
  align-self: center;
  min-height: $toggle-height;
  transition: color 200ms;

  [class^="category"] {
    text-transform: uppercase;
    font-family: "Lato-Bold";
    font-size: 12px;
    padding-left: 5px;
    padding-right: 5px;
    align-self: center;
    letter-spacing: 0.03em;
    &.category-0 {
      text-align: right;
    }
    &.category-1 {
      grid-column-start: 3; // sets second label to other side of slider
      text-align: left;
    }
  }

  /** slider reference:
  https://www.w3schools.com/howto/howto_css_switch.asp */
  /* The switch - the box around the slider */
  .switch {
    align-self: center;
    position: relative;
    display: inline-block;
    grid-column-start: 2;
    grid-row-start: 1;
    justify-self: center;
    height: 18px;
    width: 35px;
  }

  .switch input {
    opacity: 0;
  }

  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    // border: 2px white solid;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 8px;
    width: 8px;
    left: 3px;
    bottom: 3px;
    // background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(17px);
    -ms-transform: translateX(17px);
    transform: translateX(17px);
  }

  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }
}
