@import "../../styling/variables.scss";

.HelpBadge {
  position: absolute;
  right: 1em;
  top: 0.5em;
  font-family: "Lato-Bold";
  font-size: 0.8em;
  width: 230px;
  text-align: right;

  a {
    text-decoration: none;
    color: get-color();
  }
}

@media (max-width: $mobile-breakpoint) {
  .HelpBadge {
    text-transform: uppercase;
    font-family: "Lato-Black";
    font-size: 12px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    z-index: 4;
    padding: 1em;
    align-items: center;
    display: flex;
    background-color: white;
    box-shadow: 2px 3px 9px 0.25px $drop-shadow-color;
    opacity: 1;
    transition: opacity $section-transition;

    a {
      text-decoration: none;
      background-color: white;
      color: get-color();
      padding: 0;
      text-align: center;
      width: 100%;
    }

    &:not(.showBadge) {
      opacity: 0;
    }
  }
}
