// @import "./fonts.scss";
// color accessing example: https://www.sassmeister.com/gist/ac4322cc0564cd37c432
// set default color key
$color-key-base: "primary" !default;

/* colors */
$primary-palette: (
  "orange": (
    "primary": #f38b00,
    "secondary": #ffa044,
    "tertiary": #ffc48b,
    "quaternary": #ffdbb9,
  ),
  "yellow": (
    "primary": #ecbc09,
    "secondary": #f4d279,
    "tertiary": #f7dfa1,
    "quaternary": #faf6e3,
  ),
  "green": (
    "primary": #71a850,
    "secondary": #95b974,
    "tertiary": #bed3a7,
    "quaternary": #d9e4ca,
  ),
  "teal": (
    "primary": #51bf9d,
    "secondary": #7fcbb4,
    "tertiary": #aadbcc,
    "quaternary": #d4ece5,
  ),
  "blue": (
    "primary": #4ac1e0,
    "secondary": #72cee7,
    "tertiary": #ade0f0,
    "quaternary": #d1ecf6,
  ),
  "periwinkle": (
    "primary": #8890B4,
    "secondary": #9BA2C0,
    "tertiary": #C1C5D8,
    "quaternary": #E7E9F0,
  ),
  "pink": (
    "primary": #d65d72,
    "secondary": #e67f8c,
    "tertiary": #f4afb6,
    "quaternary": #fbcfd2,
  ),
  "red": (
    "primary": #ed5858,
    "secondary": #F28181,
    "tertiary": #F6ABAB,
    "quaternary": #FAD4D4,
  ),
  "purple": (
    "primary": #a367ab,
    "secondary": #b98dc0,
    "tertiary": #d1b3d5,
    "quaternary": #e8d9ea,
  ),
);

$contrast-palette: (
  "dark-blue": #0a2240,
  "dark-red": #402020,
  "dark-brown": #30261d,
  "dark-grey": #434343,
);

$text-color: #5E5E5E;
$light-text-color: #DEDEDE;
$light-grey: #EEE;
$drop-shadow-color: rgba(0, 0, 0, 0.3);

/** Funtion to access color variable*/
@function get-color(
  $palette: $primary-palette,
  $name: "orange",
  $variant: $color-key-base
) {
  $color: null;

  // Get the color spectrum
  $color-spectrum: map-get($palette, $name);

  // Get the color variant
  @if $color-spectrum {
    $color: map-get($color-spectrum, $variant);
  }

  @return $color;
}

// sizing variables
$nav-closed-width: 50px;
$mobile-nav-closed-width: 45px;
$nav-open-width: 190px;
$window-min-width: 1200px;
$mobile-breakpoint: 800px;
$header-height: 50px;
$card-height: 400px;
$title-height: 50px;
$toggle-height: 30px;
$grouped-opacity: 0.6;
$mobile-card-height: 350px;
$mobile-title-height: 75px;
$mobile-toggle-height: 50px;
$chart-min-height: $card-height - $toggle-height - $title-height;
$mobile-chart-min-height: $mobile-card-height - $mobile-toggle-height -
  $mobile-title-height;

// only used in JS
$horizchart-category-height: 20;
$horizchart-padding-height: 5;

// transition variables
$section-delay: 300ms;
$section-transition: 500ms;

:export {
  textColor: $text-color;
  orange: get-color();
  yellow: get-color($name: "yellow");
  green: get-color($name: "green");
  blue: get-color($name: "blue");
  periwinkle: get-color($name: "periwinkle");
  pink: get-color($name: "pink");
  red: get-color($name: "red");
  purple: get-color($name: "purple");
  secondaryColors: get-color($name: "red", $variant: "secondary"),
    get-color($name: "orange", $variant: "secondary"),
    get-color($name: "yellow", $variant: "secondary"),
    get-color($name: "green", $variant: "secondary"),
    get-color($name: "blue", $variant: "secondary"),
    get-color($name: "periwinkle", $variant: "secondary"),
    get-color($name: "pink", $variant: "secondary"),
    get-color($name: "red", $variant: "secondary"),
    get-color($name: "purple", $variant: "secondary"),
    get-color($name: "orange", $variant: "tertiary");
  mobileBreakpoint: $mobile-breakpoint;
  sectionDelay: $section-delay;
  cardHeight: $card-height;
  chartHeight: $chart-min-height;
  horizchartCategoryHeight: $horizchart-category-height;
  horizchartPaddingHeight: $horizchart-padding-height;
}
