@import "../../styling/variables.scss";
.NavButtons {
  padding: 0.25em;

  button {
    font-size: 0.8em;
    font-family: "Lato-Black";
    letter-spacing: 0.03em;
    color: white;
    display: flex;
    align-items: center;
    margin: 1em 0.5em;
    text-transform: uppercase;
    box-shadow: 1px 4px 9px -2.75px $drop-shadow-color;
    border: none;
    outline: none;
    cursor: pointer;
    transition: transform 200ms;

    &.download {
      padding: 0.25em;
      background-color: get-color($name: "orange", $variant: "secondary");
    }

    img {
      width: 20px;
      height: 100%;
      vertical-align: middle;
      padding: 0.25em;
    }

    span {
      margin: 0px 10px;
      text-align: left;
    }
  }

  button:hover {
    transform: scale(1.1);
  }
}

@media (max-width: $mobile-breakpoint) {
  button {
    width: 100%;
  }
}
