@import "../../styling/variables.scss";

.Card {
  position: relative;
  height: $card-height;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 1em;
  text-align: center;
  align-items: center;
  padding: 30px;

  &:last-child {
    border-bottom: none;
    margin-bottom: 2px;
  }

  .chart-wrapper {
    align-items: center;
    align-self: start;
    display: grid;
    height: inherit;
    position: relative;
    box-sizing: border-box;

    &.annotation {
      grid-auto-rows: 0 0 auto;
      pointer-events: none;
    }

    &:not(.annotation) {
      grid-auto-rows: $title-height $toggle-height $chart-min-height;
    }

    .graph-title {
      color: $text-color;
      height: $title-height;
      text-align: left;
      padding: 0 20px 0 10px;

      span {
        font-family: Lato-Regular;
        font-size: 16px;
      }

      span.legend-dot:before {
        font-size: 28px;
        line-height: 12px;
        content: "\2022";
        position: relative;
      }

      span.legend-dot:last-child:before {
        opacity: $grouped-opacity;
      }
    }

    .Toggle {
      max-height: $toggle-height + 20;
      min-height: $toggle-height;
    }

    .VerticalBarChart,
    .HorizontalBarChart {
      max-height: $card-height;
      min-height: $chart-min-height;
    }

    .annotation {
      height: 100%;
      font-family: "Asap-Regular";
      overflow-x: hidden;
      overflow-y: scroll;

      .content {
        text-align: left;
        overflow-y: visible;
        font-size: 1.5em;
        height: 100%;
        display: flex;
        flex-direction: column;

        .highlight {
          font-family: ASAP-Regular;
          .bold {
            font-family: ASAP-Bold;
          }
          &:not(:last-child) {
            padding-bottom: 1em;
          }
        }

        .text {
          font-size: 16px;
          font-family: Lato-Regular;
          margin-bottom: 20px;
          color: $text-color;
          
          .bold {
            font-family: Lato-Black;
          }
        }

        .list {
          padding-bottom: 1em;
          font-family: Lato-Regular;
          font-size: 16px;
          display: list-item;
          margin-left: 25px;
          .bold {
            font-family: Lato-Black;
          }
        }

        .footnote {
          align-self: baseline;
          color: $text-color;
          font-family: Lato-Regular;
          font-size: 12px;
        }
      }
    }
  }

  .pos-0-1 {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
  }

  .pos-0-2 {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
  }

  .pos-1-1 {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
  }

  .pos-1-2 {
    grid-column-start: 2;
    grid-column-end: end;
    grid-row-start: 1;
  }

  .pos-2-1 {
    grid-column-start: 3;
    grid-column-end: end;
    grid-row-start: 1;
  }

}

@media (max-width: $mobile-breakpoint) {
  .Card {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-row-gap: 1em;
    padding: 1em;
    height: unset;

    .pos-0-1, .pos-0-2 {
      grid-row-start: 1;
      grid-row-end: 2;
      grid-column-start: 1;
    }
    
    .pos-1-1, .pos-1-2 {
      grid-row-start: 2;
      grid-row-end: 3;
      grid-column-start: 1;
    }

    .pos-2-1 {
      grid-row-start: 3;
      grid-row-end: end;
      grid-column-start: 1;
    }

    .chart-wrapper {
      .VerticalBarChart,
      .HorizontalBarChart {
        max-height: $mobile-card-height;
        min-height: $mobile-chart-min-height;
      }

      &:not(.annotation) {
        grid-auto-rows: auto auto $mobile-chart-min-height;
      }

      .graph-title {
        height: auto;
        padding: 10px 0px;
      }
    }
  }
}
