@import "../../styling/variables.scss";

.HorizontalBarChart {
  width: 100%;
  display: grid;
  grid-auto-rows: 1fr;
  grid-row-gap: 5px;

  .label-group-div {
    max-height: 15px;
    align-self: center;
  }

  .bar-group {
    position: relative;
    display: grid;
    max-height: 50px;

    // this applies the same border style to both .dots-group and .bar for spacing / centering reasons
    div:not(.label-inline) {
      border-radius: 10px;
    }

    .bar-dots {
      position: absolute;
      height: 80%;
      bottom: 0px;
      left: -2px;
      display: grid;
      align-content: center;
    }
    .dot {
      background-color: $light-grey;
      grid-row: 1;
      display: inline-block;
      height: 6px;
      width: 6px;
      border-radius: 50%;
      margin: auto 4px;
    }

    .bar-measure {
      position: absolute;
      height: 80%;
      bottom: 0px;
      left: -2px;
      display: grid;
      grid-auto-columns: auto 45px;
      align-content: center;
      transition: width 500ms ease, opacity 300ms;
      &.outside-bar {
        .label-data {
          position: relative;
          width: inherit;
          margin: 5px;
        }
      }
    }

    .label-data {
      grid-row: 1;
      font-size: 14px;
      font-family: Lato-Black;
      border: none;
      text-align: right;
      margin: 0px 10px;
      pointer-events: none;
    }
  }
  .label-group-div {
    position: relative;
    hr {
      height: 2px;
      border: none;
    }
    .label-group {
      position: absolute;
      background-color: white;
      height: 10px;
      left: 0;
      top: 0;
      padding: 0 5px 0 0;
      font-size: 12px;
      font-family: Lato-Regular;
      text-align: right;
      z-index: 1;
    }
  }
  .label-inline {
    pointer-events: none;
    border: none;
    position: relative;
    top: 1px;
    right: 0px;
    font-size: 12px;
    text-align: right;
  }
}

@media (max-width: $mobile-breakpoint) {
  .HorizontalBarChart {
    .bar-group {
      .label-inline {
        top: 2px;
        font-size: 10px;
        line-height: 8px;
      }
    }
  }
}

