@import "../../styling/variables.scss";

.Header {
  height: $header-height - 30px;
  width: calc(100vw - 20px);
  transition: height $section-transition;
  display: flex;
  flex-direction: row;
  align-items: baseline;
  padding: 10px 10px 20px 10px;

  a {
    height: inherit;
    width: fit-content;
    .logo {
      height: inherit;
    }
  }

  .title {
    vertical-align: baseline;
    height: fit-content;
    color: get-color();
    font-family: Lato-Bold;
    font-size: 1.75vw;

    span {
      padding: 10px;
    }
  }
}

@media (max-width: $mobile-breakpoint) {
  .Header {
    .title {
      display: none;
    }
    &.scrolledPastIntro {
      height: 0;
      padding: 0px;
    }
  }
}
