@import "../../styling/variables.scss";

.TextCard {
  padding: 2em;
  font-family: Lato-Regular;

  .title {
    font-family: "Lato-Bold";
    font-size: 2em;
    padding-bottom: 0.5em;
  }

  .headline {
    font-family: ASAP-Regular;
    font-size: 2em;
    width: 66%;
    .bold {
      font-family: ASAP-Bold;
    }
  }

  .nested-row-section {
    display: inline-grid;
    padding: 10px 0px;
    grid-column-gap: 20px;

    &.many-columns {
      padding-bottom: 0;
      :not(:last-child) {
        margin-bottom: 1em;
      }
    }
  }

  .text-content {
    columns: 3;

    .text, .list {
      color: $text-color;
    }

    .bold {
      font-family: Lato-Black;
    }

    .italics {
      font-family: Lato-Italic;
    }

    div {
      padding-bottom: 0.75em;
    }
  }

  .highlight {
    font-family: "ASAP-Regular";
    font-size: 1.5em;
  }

  .subtitle {
    min-height: 2.8em;
    font-size: 1.25em;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    span {
      height: fit-content;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 0.03em;
      margin: 10px;
      font-family: Lato-Black;
      font-size: 0.7em;
      color: white;
    }
  }

  .list {
    display: list-item;
    margin-left: 25px;
  }

  .signature {
    padding-left: 20px;
  }

  &:last-child {
    border-bottom: none;
    margin-bottom: 2px;
  }
}

@media (max-width: $mobile-breakpoint) {
  .TextCard {
    padding: 1em;
    .headline {
      width: 100%;
    }
    .text-content {
      columns: 1;
    }
    .nested-row-section {
      display: inherit;
    }
    .title {
      font-size: 2em;
    }
    .text {
      columns: unset;
    }
    .subtitle {
      justify-content: flex-start;
      span {
        text-align: left;
        width: 100%;
      }
    }
  }
}
