@import "../../styling/variables.scss";

.Section {
  position: relative;
  padding: 0 2em;

  .section-title {
    font-family: "ASAP-Bold";
    text-transform: uppercase;
    text-align: center;
    font-size: 1.5em;
    padding: 0.5em;
    position: sticky;
    top: 0;
    z-index: 2;
    box-shadow: 0 8px 8px -8px $drop-shadow-color;
    background-color: white;
  }

  .two-n-logo {
    height: 30px;
    padding: 1em;
    text-align: right;
    display: grid;
    grid-template-columns: auto 40px;

    span {
      align-self: center;
      font-size: 16px;
      font-family: "ASAP-Regular";
    }

    svg {
      margin: 0px 2px;
    }

    circle {
      fill: $text-color;
    }

    path {
      fill: white;
    }
  }
}

@media (max-width: $mobile-breakpoint) {
  .Section {
    padding: 0em 0.5em 0 0.5em;

    .section-title {
      padding: 0.5em 0;
      font-size: 18px;
    }
  }
}
