@import "../../styling/variables.scss";

.SocialButtons {
  position: absolute;
  right: 4em;
  top: 6em;
  width: fit-content;
  cursor: pointer;

  svg {
    opacity: 0.5;
  }

  svg:hover {
    transform: scale(1.2);
    opacity: 1;
    transition-property: transform, opacity;
    transition-duration: 300ms;
  }

  .social-button {
    padding: 0 0.25em;
  }

  .copy-text {
    position: absolute;
    color: white;
    font-family: "ASAP-Regular";
    line-height: 31px;
    font-size: 14px;
    width: 30px;
    height: 31px;
    border-radius: 30px;
    z-index: -1;
    opacity: 0;
    transition: width 200ms, opacity, 300ms;
    overflow: hidden;

    &.linkCopied {
      text-align: center;
      opacity: 1;
      z-index: 1;
      width: 110px;
    }
  }

  input {
    opacity: 0;
    position: absolute;
    right: -9999px;
  }
}

@media (max-width: $mobile-breakpoint) {
  .SocialButtons {
    position: static;
    padding-top: 0.5em;
    height: fit-content;
    svg:hover {
      transform: none;
      opacity: 1;
      transition-property: opacity;
      transition-duration: 300ms;
    }
  }
}
