@import "../../styling/variables.scss";

.WordCloud {
  width: 100%;
  height: $chart-min-height + $toggle-height;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  font-size: 1em;
  align-items: center;

  .speech-bubble {
    height: 1em;
    position: relative;
    border-radius: 0.4em;
    background-color: get-color($name: "red"); // this chart only exists in the red section
    color: white;
    font-family: "ASAP-Regular";
    margin: 5px;
    padding: 8px;
    box-shadow: 1px 4px 9px -2.75px $drop-shadow-color;
    cursor: default;
    transition: transform 300ms;

    &:hover {
      transform: scale(1.2)
    }
  }

  .speech-bubble:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 40%;
    width: 0;
    height: 0;
    border: 0.5em solid transparent;
    border-top-color: get-color($name: "red");  // this chart only exists in the red section
    border-bottom: 0;
    border-left: 0;
    fill: none;
    margin-left: -0.8em;
    margin-bottom: -0.5em;
  }
}

@media (max-width: $mobile-breakpoint) {
  .WordCloud {
    height: $mobile-chart-min-height + $mobile-toggle-height;

    .speech-bubble {
      top: -30px;
      height: 1em;
      margin: 2px;
      font-size: 0.8em;
    }
  }
}
