@import "../../styling/variables.scss";

.PieChart {
  display: grid;

  svg {
    overflow: visible;
  }

  .pie-path {
    stroke: white;
    stroke-width: 0px;
    stroke-linejoin: round;

    &:hover {
      transform: scale(1.25);
      transition: transform 200ms;
    }
  }

  .label-group {
    transition-property: transform;
    transition-duration: 500ms;
    display: flex;
    flex-direction: column;

    foreignObject {
      overflow: visible;
      transition: x, y;
    }
    &.show {
      [class^="pie-label"] {
        opacity: 1;
      }
    }
  }

  .pie-label {
    position: fixed;
    pointer-events: none;
    transition: opacity 300ms;
    font-size: 14px;
    opacity: 0;
    width: 80px;
    min-width: fit-content;
    color: $text-color;
    display: flex;
    flex-direction: column;
    text-align: center;
    /* the following are specific based to the data labels presented, and should be tweaked. 
    ** these assume a smaller number label div below the label, so top level pie labels can be closer 
    ** to the circle since the number has a free corner on bottom left and right sides, 
    ** and bottom level pie labels should be farther to account for text spanning entire width. */
    &.s1 {
      transform: translate(-15%, -90%);
    }
    &.s2 {
      transform: translate(-15%, -85%);
    }
    &.s3 {
      transform: translate(0%, 0%);
    }
    &.s4 {
      transform: translate(-10%, -10%);
    }
    &.s5 {
      transform: translate(-100%, -10%);
    }
    &.s6 {
      transform: translate(-95%, -5%);
    }
    &.s7 {
      transform: translate(-93%, -85%);
    }
    &.s8 {
      transform: translate(-90%, -90%);
    }
    &.flatLabel {
      transform: translate(-100%, -100%);
      width: max-content;
      text-align: right;
    }
    &.short {
      width: 50px
    }
    &.long {
      width: 175px;
    }
    /* custom fix for 2020 */ 
    &.nudgeUp {
      top: -10px;
    }

  }

  .pie-label-text {
    font-family: Lato-Regular;
  }

  .pie-label-value {
    font-family: Lato-Black;
  }
}

@media (max-width: $mobile-breakpoint) {
  .PieChart {
    svg {
      width: 200px;
      transform: scale(0.8);

      .pie-path:hover {
        transform: none;
      }
    }
  }
}
