@import "../../styling/variables.scss";

.GroupedVerticalBar {
  display: grid;
  height: 100%;

  &.vertical-many, &.vertical-no-domain {
    display: grid;
    // twice as wide for grouped charts
    grid-auto-columns: minmax(1fr, 80px);
    grid-template-rows: 100%;
    grid-column-gap: auto;
    .bar-group {
      grid-row: 1;
      display: grid;
      grid-template-columns: 100%;
      grid-template-rows: 90% 10%;
      justify-items: center;

      .bar-dots,
      .bar-measure {
        grid-row: 1/2;
      }
      .bar-dots {
        height: 100%;
        overflow: hidden;
      }
      .label-measure {
        grid-row: 2;
        top: 5%;
        padding: 0px 5px;
      }
    }
  }

  .bar-group {
    position: relative;
    width: 100%;

    [class^="bar"] {
      position: absolute;
      transition: height 500ms ease;
      border-radius: 10px;
      bottom: 0;
    }

    [class^="label"] {
      position: absolute;
    }

    .label-measure {
      font-size: 16px;
      font-family: Asap-Regular;
      bottom: 0;
    }

    .label-data {
      font-size: 14px;
      font-family: Lato-Black;
      width: 100%;
      text-align: center;
      margin-top: 7px;
      &.mid {
        margin-top: 4px;
      }
      &.small {
        display: none;
      }
    }

    .bar-measure {
      width: 40%; /* this allows for two bars, 50%, with a 10% spacing */
      max-width: 50px;
      
      &.pos-left {
        right: 52%;
      }
      &.pos-right {
        left: 52%;
        opacity: $grouped-opacity;
        
        .label-data {
          color: black
        }
      }
    }

    .label-subset {
      font-family: Asap-Regular;
    }

    &:not(.vertical-many) {
      &.pos-center {
        .label-measure,
        .label-subset {
          text-align: center;
        }
      }

    }

    &.subset {
      [class^="bar"] {
        border: none;
      }
      .bar-fill {
        height: 100%;
      }
    }

    &.vertical-two,
    &.vertical-many {
      .dot {
        background-color: $light-grey;
        display: block;
        height: 10px;
        width: 10px;
        border-radius: 50%;
        margin: 0px 20px 10px 20px;
      }
    }
  }
}

@media (max-width: $mobile-breakpoint) {
  .GroupedVerticalBar {
    width: 100%;
    .bar-group {
      .label-data {
        font-size: 12px;
        &.mid {
          margin-top: 1px;
        }
      }
    }
  }
}
