@import "../../styling/variables.scss";

.VerticalBarChart {
  display: grid;
  height: 100%;
  margin: auto;

  &.vertical-many, &.vertical-no-domain {
    width: fit-content;
    display: grid;
    grid-auto-columns: minmax(60px, 1fr);
    grid-template-rows: 100%;
    grid-column-gap: 10px;
    .bar-group {
      grid-row: 1;
      display: grid;
      grid-template-columns: 100%;
      grid-template-rows: 80% 20%;
      justify-items: center;

      .bar-dots,
      .bar-measure {
        grid-row: 1/2;
      }
      .bar-dots {
        height: 100%;
        overflow: hidden;
      }
      .label-measure {
        grid-row: 2;
        top: 5%;
      }
    }
  }
  &.vertical-two,
  &.subset {
    grid-template-columns: 50% 50%;
    width: 90%;
  }

  .bar-group {
    position: relative;
    width: 100%;

    [class^="bar"] {
      position: absolute;
      transition: height 500ms ease;
      border-radius: 10px;
      bottom: 0;
    }

    [class^="label"] {
      position: absolute;
    }

    .label-measure {
      font-size: 14px;
      bottom: 0px;
    }

    .label-data {
      font-size: 14px;
      font-family: Lato-Black;
      width: 100%;
      text-align: center;
      margin-top: 7px;
      &.mid {
        margin-top: 4px;
      }
      &.small {
        display: none;
      }
    }

    .label-subset {
      font-family: Asap-Regular;
      bottom: 0;
    }

    .bar-measure {
      width: 100%;
      max-width: 50px;
    }

    &:not(.vertical-many) {
      &.pos-center {
        .bar-measure,
        .bar-fill,
        .bar-dots {
          bottom: 0;
        }
        .label-measure,
        .label-subset {
          text-align: center;
        }
      }

      &.pos-left {
        .bar-measure,
        .bar-fill,
        .bar-dots {
          left: calc(100% - 58px);
          bottom: 0,
        }
        .label-measure,
        .label-subset {
          width: calc(100% - 68px);
          text-align: right;
        }
        .long {
          // if there is overflow, this keeps it right aligned in the div
          direction: rtl;
        }
      }

      &.pos-right {
        .bar-measure,
        .bar-fill,
        .bar-dots {
          left: 2px;
          bottom: 0,
        }
        .label-measure,
        .label-subset {
          left: 65px;
          width: calc(100% - 65px);
          text-align: left;
        }
      }
    }

    &.subset {
      [class^="bar"] {
        border: none;
      }
      .bar-fill {
        height: 100%;
      }
    }

    .bar-dots {
      height: 100%;
    }

    &.vertical-two,
    &.vertical-many {
      .dot {
        background-color: $light-grey;
        display: block;
        height: 6px;
        width: 6px;
        border-radius: 50%;
        margin: 0px 20px 8px 20px;
      }
    }
  }
}

@media (max-width: $mobile-breakpoint) {
  .VerticalBarChart {
    &.vertical-two,
    &.vertical-many,
    &.vertical-no-domain,
    &.subset {
      width: 90%;
      grid-auto-columns: 1fr;
    }
    .bar-group {
      .label-data {
        &.mid {
          margin-top: 1px;
        }
      }
    }
  }
}
