@import "../../styling/variables.scss";

.Navigation {
  width: $nav-closed-width;
  height: inherit;
  display: grid;
  grid-template-columns: 1fr;
  cursor: pointer;
  z-index: 3;
  color: white;

  .nav-tab-parent {
    position: relative;
    width: $nav-open-width;
  }

  .nav-tab-back {
    position: absolute;
    top: 0;
    width: $nav-closed-width;
    height: 100%;
    transition: border-width 300ms, background-color 300ms;
  }

  .nav-tab-front {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    grid-template-columns: $nav-closed-width 1fr;
    text-transform: uppercase;
    display: grid;
    align-items: center;
    font-family: "Lato-Black";
    font-size: 12px;
    text-align: center;

    img {
      transition: transform 300ms;
      margin-left: 10px;
      width: 30px;
    }

    span {
      transition: opacity 200ms;
      opacity: 0;
      margin: 0px 5px;
    }

    &:hover {
      img {
        transform: scale(1.3);
      }
    }

  }

  &.isOpen {
    box-shadow: 2px 0px 10px 0px $drop-shadow-color;

    .nav-tab-front {
      opacity: 1;
    }
    span {
      position: relative;
      pointer-events: none;
      opacity: 1;
    }

    .nav-tab-back {
      border-right-width: ($nav-open-width - $nav-closed-width);
    }

  }

  &:focus {
    outline: none;
  }
}
@media (max-width: $mobile-breakpoint) {
  .Navigation {
    width: $mobile-nav-closed-width;
    .nav-tab {
      img {
        margin-left: 7px;
      }
    }
  }
}
